body{
    height: 1fr;
}
.container-1{
    font-size: 18px;
    font-style:normal;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    height: 50px;
    display: flex;
    align-items: center;
}
.menu>div:hover{
    background-color: rgb(5, 57, 59);
}
.menu{
    width: 1fr;
    display: flex;
    justify-content: space-between;
    background-color: rgb(14, 137, 137);
    height: 28px;
    color: white;
}
span{
    display: flex;
    width: 100px;
    height: 100%;
    align-items: center;
    justify-content: center;
    
}
@media screen and (max-width: 450px){
    div.menu{
        display: flex;
        width: 1fr;
        height: 150px;
        flex-direction: column;
        align-items:center;
        justify-content: space-around;
    }
    .menu1{
        display: flex;
        justify-content: space-around;
        width: 100%;
        height: 450px;
    }
}
